import React, { useState } from 'react'
import { NavLink, Route } from 'react-router-dom'
import styled from 'styled-components'
import RrNoticeConts from './RrNoticeConts'

const data = [
  { date: '03.20', desc: 'Rrrr Ïã†Í∑ú ÌîÑÎ°úÎ™®ÏÖò ÏïàÎÇ¥' },
  { date: '03.17', desc: 'ÏÑúÎπÑÏä§ Ï†êÍ≤Ä ÏïàÎÇ¥ (3/13(ÌÜ†) 00:00 ~ 01:00))' },
  { date: '03.15', desc: 'Rrrr Ï†ïÏ±Ö Î≥ÄÍ≤Ω ÏïàÎÇ¥' },
  { date: '03.15', desc: 'Ïï± ÎÇ¥ Í≥†Í∞ùÏÑºÌÑ∞ Ïò§Ìîà ÏïàÎÇ¥' },
  { date: '03.13', desc: 'ÏÑúÎπÑÏä§ Ïò§Ìîà ÏïàÎÇ¥' },
]

const RrNotice = () => {
  const [showCont, setShowCont] = useState(false)
  const [clickNotice, setClickNotice] = useState()

  const onClickNotice = list => {
    setShowCont(true)
    setClickNotice(list)
  }

  const setShowStatus = (data: boolean) => {
    setShowCont(data)
  }

  return (
    <RrNoticeWrapper>
      {!showCont ? (
        data.map((list, index) => (
          <NoticeList key={index}>
            <Date>{list.date}</Date>
            <Description
              to="/my/notice/conts"
              onClick={() => onClickNotice(list)}
            >
              {list.desc}
            </Description>
          </NoticeList>
        ))
      ) : (
        <Route
          path="/my/notice/conts"
          render={() => (
            <RrNoticeConts showFunction={setShowStatus} data={clickNotice} />
          )}
        />
      )}
    </RrNoticeWrapper>
  )
}

export default RrNotice

const RrNoticeWrapper = styled.ul`
  padding-left: 0;
  padding-top: 10px;
  padding-bottom: 10px;

  position: relative;
`

const NoticeList = styled.li`
  border-bottom: 1px solid #e0e0e0;
  padding: 22px 16px 19px;
  list-style: none;
`

const Date = styled.span`
  font-size: 12px;
  line-height: 14px;
  letter-spacing: 0.122727px;
  color: #bdbdbd;
  padding-right: 20px;
`

const Description = styled(NavLink)`
  font-size: 15px;
  line-height: 20px;
  color: #222;
  text-decoration: none;
`
