const IndexPage = () => {
  const [alarms, setAlarms] = useState<Alarm[]>([])

  const history = useHistory()
  console.log(history)

  const repository = useMemo(() => new AlarmRepository(), [])

  const fetchAlarms = useCallback(async () => {
    setAlarms(await repository.FetchAlarms())
  }, [repository])

  useEffect(() => {
    fetchAlarms()
  }, [fetchAlarms])

  return (
    <>
      <Banner>
        <Img
          src={process.env.PUBLIC_URL + '/image/feed.png'}
          alt="ÌîºÎìú Ïù¥ÎØ∏ÏßÄ"
        />
      </Banner>
      <ContentWrapper>
        <RrSection title="ÌÖåÎßà RRRR" className="mt-60" />
        <CategoryListWrapper>
          {categoryList.map(category => (
            <RrCategoryCard title={category.title} content={category.content} />
          ))}
        </CategoryListWrapper>
        <RrSection
          title="Ïù∏Í∏∞ RRRR"
          className="mt-60"
          onClick={() => history.push('/alarmdrawer')}
        />
        <AlarmListWrapper>
          {alarms.map(alarm => (
            <RrAlarmCard alarm={alarm} />
          ))}
        </AlarmListWrapper>
      </ContentWrapper>
    </>
  )
}
export default IndexPage

